/* src/components/Login/Login.css */

body {
    margin: 0;
    font-family: 'Roboto', sans-serif; /* Use a modern font */
    background: linear-gradient(135deg, #6b73ff, #000dff); /* Gradient background */
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh; /* Full height of the viewport */
}

.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.login-box {
    background: white; /* White background for the login box */
    padding: 40px;
    border-radius: 12px; /* Rounded corners */
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2); /* Soft shadow */
    max-width: 400px;
    width: 100%; /* Full width of the box */
}

h2 {
    text-align: center;
    color: #333;
    margin-bottom: 20px; /* Spacing below the heading */
    font-weight: bold; /* Bold heading */
}

.input-group {
    margin-bottom: 15px; /* Spacing between input groups */
}

label {
    display: block;
    margin-bottom: 5px; /* Spacing below labels */
    color: #555; /* Label color */
}

input {
    width: 100%;
    padding: 12px; /* Increased padding */
    border: 1px solid #ccc;
    border-radius: 8px; /* More rounded corners */
    font-size: 16px; /* Increase font size */
    transition: border-color 0.3s; /* Smooth transition */
}

input:focus {
    border-color: #6b73ff; /* Blue border on focus */
    outline: none; /* Remove outline */
}

button {
    width: 100%;
    padding: 12px; /* Increased padding */
    background-color: #6b73ff; /* Primary button color */
    color: white;
    border: none;
    border-radius: 8px; /* Rounded corners */
    cursor: pointer;
    font-size: 16px; /* Increase font size */
    transition: background-color 0.3s; /* Smooth transition */
}

button:hover {
    background-color: #5a63e5; /* Darker blue on hover */
}

.error {
    color: red;
    text-align: center; /* Center the error message */
    margin-top: 10px; /* Space above the error message */
}

.signup-link {
    text-align: center;
    margin-top: 15px; /* Space above the link */
}

.signup-link a {
    color: #6b73ff; /* Link color */
    text-decoration: none; /* Remove underline */
}

.signup-link a:hover {
    text-decoration: underline; /* Underline on hover */
}